import React from 'react';
import {
  Input,
  Form
} from 'antd'

const FormItem = Form.Item;
export default class Uploader extends React.Component {
  render() {
    return (
      <Form horizontal>
        <FormItem
          label="账户"
          labelCol={{ span: 6 }}
          wrapperCol={{ span: 3 }}
        >
          <Input placeholder="请输入账户名"
          />
        </FormItem>
  		</Form>
    )
  }
}


